  padding: 0 2rem;
  max-width: 140rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  grid-template-rows: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-template-areas:
    ". . . .";
  align-items: start;
  text-align: center;
}

